body {
    background-color: orangered;
}
.lightRoot {
    margin-top: 40px;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 100px;
}
.lightsFlex {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    width: 70%;
    gap: 100px;
}

.absoluteSolar {
    position: absolute;
    top: -20px;
    left: -50px;
    z-index: 1;

}
.solar {
    z-index: 10;
    position: relative;
}

.absoluteRoot {
    margin-top: 300px;
    position: relative;
    margin-bottom: 200px;
}

.sun {
    position: absolute;
    width: 200px;
    right: 20px;
    top: 30px;
}

.result {
    text-align: center;
    margin-top: 300px;
    font-size: 100px;
}