.animation {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 150px;
    opacity: 0;
    transition: opacity 0.5s, transform 0.5s;
    z-index: 999;
    -webkit-text-stroke: 8px #FFFFFF;
}

.animation.enter {
    opacity: 1;
    transform: translate(-50%, -50%) scale(1);
}

.animation.exit {
    opacity: 0;
    transform: translate(-50%, -50%) scale(0.5);
}
