/* components/StartScreen.css */
.start-screen {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;

}

.start-button {
    padding: 20px 40px;
    font-size: 50px;
    cursor: pointer;
    background-color: orangered;
    outline: none;
    color: white;
    border: 3px solid white;
}

.start-screen p {
    max-width: 700px;
    font-size: 30px;
}

.rules-button {
    margin-left: 40px;
    font-size: 20px;
    color: #FFFFFF;
    border: none;
    background-color: orangered;
    cursor: pointer;
    padding: 10px;
}

.close-button {
    font-size: 20px;
    color: #FFFFFF;
    border: none;
    background-color: orangered;
    cursor: pointer;
    padding: 10px;
}

.rules-modal {
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: orangered;
}